<template>
    <div class="content">
      <div class="content_name" @click="find_address">点击定位：</div>
      <div class="ding_wei_msg">邮政编码：{{ding_wei_msg.adCode}}</div>
      <div class="ding_wei_msg">国家：{{ding_wei_msg.nation}}</div>
      <div class="ding_wei_msg">城市：{{ding_wei_msg.city}}</div>
      <div class="ding_wei_msg">详细：{{ding_wei_msg.addr}}</div>
      <div class="ding_wei_msg">精度：{{ding_wei_msg.lat}}</div>
      <div class="ding_wei_msg">维度：{{ding_wei_msg.lng}}</div>
      <div class="ding_wei_msg">距离八音：{{juli}}Km</div>
    </div>
</template>

<script type="es6">
export default {
  name: 'index',
  data () {
    return {
      ding_wei_msg:{
        adCode : "",
        nation : "",
        city : "",
        addr : "",
        lat : "",
        lng : ""
      },
      juli:""
    }
  },
  methods:{
    //定位
    find_address:function(){
      var that = this;
      var geolocation = new qq.maps.Geolocation("OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77", "bayin");
      var positionNum = 0;
      var options = {timeout: 8000};
      function showPosition(position) {
        var adCode = position.adCode;//邮政编码
        var nation = position.nation;//中国
        var city = position.city; //城市
        var addr = position.addr; //详细地址
        var lat = position.lat; //
        var lng = position.lng; //火星坐标
        var detailsAddress = city + addr;
        var self_jl = ju_li(lng,lat);
        that.ding_wei_msg.adCode = adCode;
        that.ding_wei_msg.nation = nation;
        that.ding_wei_msg.city = city;
        that.ding_wei_msg.addr = addr;
        that.ding_wei_msg.lat = lat;
        that.ding_wei_msg.lng = lng;
        that.juli = self_jl.toFixed(2);
      }
      function showErr() {
        alert("定位失败，请稍后再试...");
      }
      function dingwei_c(){
        //定位
        geolocation.getLocation(showPosition, showErr, options);
      }
      dingwei_c();
      //定位后计算距离
      function ju_li(self_lng,self_lat){
        var lat,lng;
        lat = 34.169022;
        lng = 108.960750;;
        return Math.sqrt(Math.pow((lat - self_lat),2) + Math.pow((lng - self_lng),2))*100;
      }
    }
  }
}

</script>

<style scoped>
  .content {
    padding: .2rem;
    background: #dddddd;
  }
  .content_name {
    font-size: .18rem;
    font-weight: bold;
    color: #00e0a8;
    text-align: center;
    border: 2px solid #00e0a8;
    border-radius: .05rem;
    width: 50%;
    padding: 0 0.1rem;
    margin: 0 auto;
  }
  .ding_wei_msg {
    font-size: .13rem;
    color: #333333;
    opacity: 0.6;
    font-weight: bold;
    margin-top: .11rem;
  }
</style>



