<template>
  <div class="content">
    <!-- 学校图片-->
    <div class="school_img">
      <img src="/static/images/test.jpg" alt="图片"/>
    </div>
    <!-- 学校名称-->
    <div class="school_info">
      <p class="school_name">西安市航天小学</p>
      <p class="school_lev"><span class="school_lev_img"><img src="/static/images/school_lev.png"/></span>距您12.12km</p>
      <p class="self_address"><span class="self_address_img"><img src="/static/images/school_self_address.png"/></span>西安市友谊西路127号</p>
    </div>
    <!-- 学校介绍-->
    <div class="school_details">
      <p class="ci_title">学校介绍</p>
      <p class="school_details_content">西安市航天小学始建于1969年，现坐落在西安南郊航天城内。学校占地面积21686平方米，在校学生近1900名。 学校师资队伍优秀，教学设施精良。学校先后被授予“西安市一级小学”、“中国航天总公司一类学校”、“中华青少年航天科普网网点学校”、 “全国小学生英语竞赛优胜学校” 、“全国义务教育语文教育研究中心试验学校”等多种荣誉称号。</p>
      <p class="school_details_content_more"><button class="school_details_content_more_btn" @click="school_details">查看更多</button></p>
    </div>
    <!-- 艺术课程-->
    <div class="art_class">
      <div class="ci_title">艺术课程</div>
      <div class="art_class_list" v-for="art in art_class_list">
        <div class="art_class_all">
          <div class="art_class_img"><img :src="art.src" alt="课程"/></div>
          <div class="art_class_msg">
            <div class="art_class_name">{{art.name}}</div>
            <div class="art_class_details">{{art.details}}</div>
            <div class="art_class_mf_img"><img :src="art.is_free"/></div>
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script type="es6">
export default {
  name: 'index',
  data () {
    return {
        art_class_list:[
          {
            name: "长笛考级一级音阶初级培训",
            details: "暂无描述",
            src: "/static/images/default.jpg",
            is_free: "/static/images/school_class_le.png"
          },
          {
            name: "长笛考级二级音阶初级培训",
            details: "啦啦啦啦啦啦水水的撒低级阿三发射点法术等级",
            src: "/static/images/test.jpg",
            is_free: "/static/images/school_class_le.png"
          },
          {
            name: "长笛考级三级音阶初级培训",
            details: "啦啦啦啦啦啦水水的撒低级阿三发射点法术等级啦啦啦啦啦啦水水的撒低级阿三发射点法术等级",
            src: "/static/images/default.jpg",
            is_free: "/static/images/school_class_le.png"
          },
          {
            name: "长笛考级四级音阶初级培训",
            details: "啦啦啦啦啦啦水水的撒低级阿三发射点法术等级啦啦啦啦啦啦水水的撒低级阿三发射点法术等级啦啦啦啦啦啦水水的撒低级阿三发射点法术等级",
            src: "/static/images/test.jpg",
            is_free: "/static/images/school_class_le.png"
          },
          {
            name: "长笛考级五级音阶初级培训",
            details: "啦啦啦啦啦啦水水的撒低级阿三发射点法术等级啦啦啦啦啦啦水水的撒低级阿三发射点法术等级啦啦啦啦啦啦水水的撒低级阿三发射点法术等级啦啦啦啦啦啦水水的撒低级阿三发射点法术等级啦啦啦啦啦啦水水的撒低级阿三发射点法术等级",
            src: "/static/images/default.jpg",
            is_free: "/static/images/school_class_le.png"
          }
        ]
    }
  },
  methods:{
    //学校详情介绍
    school_details:function(){
      var sas = $('.school_details_content_more_btn').text();
      if(sas == "查看更多") {
        $('.school_details_content').attr('style', 'max-height:666rem;');
        $('.school_details_content_more_btn').text("收起更多");
      }else{
        $('.school_details_content').attr('style', 'max-height:0.61rem;');
        $('.school_details_content_more_btn').text("查看更多");
      }
    }
  }
}

</script>

<style scoped>
  .school_img {
    width: 100%;
    height: 1.8rem;
  }
  .school_info {
    background: #ffffff;
    padding: 0.19rem 0.15rem;
    padding-bottom: 0.08rem;
    margin: 0.08rem 0;
    color: #000000;
  }
  .school_name {
    opacity: 0.85;
    font-size: 0.14rem;
    font-weight: bold;
    margin-bottom: 0.03rem;
  }
  .school_lev {
    opacity: 0.45;
    font-size: 0.12rem;
    margin-bottom: 0.04rem;
  }
  .self_address {
    opacity: 1;
    font-size: 0.13rem;
    color: #00e0A8;
  }
  .school_lev_img,.self_address_img {
    display: inline-block;
    width: 0.12rem;
    height: 0.12rem;
    margin-right: 0.03rem;
  }
  .school_details {
    background: #ffffff;
    margin: 0.06rem 0;
  }
  .school_details_content {
    padding: 0.1rem 0.15rem;
    padding-right: 0.08rem;
    opacity: 0.85;
    font-size: 0.12rem;
    color: #000000;
    overflow: hidden;
    max-height: 0.61rem;
  }
  .art_class_list {
    padding: 0.06rem 0.15rem;
    padding-right: 0.1rem;
    background: #ffffff;
  }
  .art_class_all {
    display: flex;
    margin-bottom: 0.08rem;
  }
  .art_class_img {
    width: 1.3rem;
    height: 0.85rem;
  }
  .art_class_img img {
    position: relative;
    /*top: -0.215rem;*/
    left: 0;
  }
  .art_class_msg {
    width: 55%;
    padding-left: 0.1rem;
    position: relative;
  }
  .art_class_name {
    font-size: 0.12rem;
    font-weight: bold;
    margin-bottom: 0.03rem;
  }
  .art_class_details {
    font-size: 0.09rem;
    max-height: 0.4rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }
  .art_class_mf_img {
    width: 0.31rem;
    height: 0.13rem;
    position: absolute;
    bottom: 0.1rem;
    right: 0.1rem;
  }
  .school_details_content_more {
    background: #ffffff;
    padding: 0.08rem 0;
    text-align: center;
    height: 0.4rem;
    position: relative;
  }
  .school_details_content_more_btn {
    font-size: 0.1rem;
    color: #00e0A8;
    border: 1px solid #00e0A8;
    background: #ffffff;
    padding: 0.01rem 0.05rem;
    border-radius: 0.15rem;
    display: inline-block;
    position: absolute;
    top: 0.13rem;
    left: 42%;
  }
</style>



