import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import CourseOnline from '@/components/CourseOnline'
import GoodsDetails from '@/components/GoodsDetails'
import OutFit from '@/components/OutFit'
import OutFit_axe from '@/components/OutFit_axe'
import OutFit_offline from '@/components/OutFit_offline'
import ShopDetails from '@/components/ShopDetails'
import SmallSchool from '@/components/SmallSchool'
import ding_wei from '@/components/ding_wei'
import ding_wei_last from '@/components/ding_wei_last'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/CourseOnline',
      name: 'CourseOnline',
      component: CourseOnline
    },
    {
      path: '/GoodsDetails',
      name: 'GoodsDetails',
      component: GoodsDetails
    },
    {
      path: '/OutFit',
      name: 'OutFit',
      component: OutFit
    },
    {
      path: '/OutFit_axe',
      name: 'OutFit_axe',
      component: OutFit_axe
    },
    {
      path: '/OutFit_offline',
      name: 'OutFit_offline',
      component: OutFit_offline
    },
    {
      path: '/ShopDetails',
      name: 'ShopDetails',
      component: ShopDetails
    },
    {
      path: '/SmallSchool',
      name: 'SmallSchool',
      component: SmallSchool
    },
    {
      path: '/ding_wei',
      name: 'ding_wei',
      component: ding_wei
    },
    {
      path: '/ding_wei_last',
      name: 'ding_wei_last',
      component: ding_wei_last
    }
  ]
})



