<template>
<div class="content">
  <!-- 店铺图片、名称-->
  <div>
    <div class="top_img"><img src="/static/images/default.jpg" alt="乐器"/></div>
    <div class="top_txt">
      <div class="top_txt_l">
        <p class="top_txt_l_title">子乐八音</p>
        <p>
          <span class="top_txt_l_sales">月销4888笔</span>
        </p>
      </div>
      <div class="top_txt_r"><a href="tel://18329798514 "><img src="/static/images/phone_icon.png" alt="打电话"/></a></div>
    </div>
  </div>
  <!-- 商品介绍-->
  <div class="goods_details">
    <p class="ci_title">店铺介绍</p>
    <p class="goods_details_content">萨克斯（英语：Saxophone，简称：Sax。）是一种木管乐器，但是管体通常是用黄铜制造，使得萨克斯管同时具有铜管类乐器的特性。有意思的是这点常造成归类上的分歧。从发声原理上来看萨克斯管和同样使用单簧片的单簧管更接近，所以一般归类为木管乐器。萨克斯管同时也是一种移调乐器，常见调性为降E和降B，也有C调和F调。萨克斯管以其烟斗状的外形为人所熟知。</p>
    <p class="goods_details_content_more"><button class="goods_details_content_more_btn" @click="goods_details">查看更多</button></p>
  </div>
  <!-- 店铺商品-->
  <div class="you_like">
    <div class="ci_title">店铺商品</div>
    <div class="like_goods" v-for="goods in shop_goods_details">
      <div class="like_img"><img :src="goods.src" alt="乐器"/></div>
      <div class="like_txt">
        <p class="like_goods_title">{{goods.name}}</p>
        <p class="like_goods_pri"><span class="like_goods_price">￥{{goods.price}}</span><span class="like_goods_sales">{{goods.sales}}人购买</span></p>
      </div>
    </div>
    <div style="clear: both;"></div>
  </div>
</div>
</template>

<script type="es6">
export default {
  name: 'index',
  data () {
    return {
      shop_goods_details:[
        {
          name: "次号考试一级专用1",
          price: "1999",
          sales: "888",
          src: "static/images/default.jpg"
        },
        {
          name: "次号考试一级专用2",
          price: "2999",
          sales: "666",
          src: "/static/images/default.jpg"
        },
        {
          name: "次号考试一级专用3",
          price: "3999",
          sales: "543",
          src: "/static/images/default.jpg"
        }
        ,{
          name: "次号考试一级专用4",
          price: "4999",
          sales: "889",
          src: "/static/images/default.jpg"
        },
        {
          name: "次号考试一级专用5",
          price: "5999",
          sales: "1245",
          src: "/static/images/default.jpg"
        }
      ]
    }
  },
  methods:{
    //商品详情介绍
    goods_details:function(){
      var sas = $('.goods_details_content_more_btn').text();
      if(sas == "查看更多") {
        $('.goods_details_content').attr('style', 'max-height:666rem;');
        $('.goods_details_content_more_btn').text("收起更多");
      }else{
        $('.goods_details_content').attr('style', 'max-height:0.61rem;');
        $('.goods_details_content_more_btn').text("查看更多");
      }
    }
  }
}

</script>

<style scoped>
  .top_img {
    width: 100%;
    height:1.801rem;
  }
  .top_txt {
    display: flex;
    font-size: .16rem;
    background: #ffffff;
  }
  .top_txt_l {
    width: 2.75rem;
    padding-top: .13rem;
    padding-bottom: .1rem;
    padding-left: .15rem;
  }
  .top_txt_l_sales {  color: #000000;   font-size: .11rem;  }
  .top_txt_l_title {  color: #000000; font-size: .17rem;  font-weight: bold;  }
  .top_txt_r {
    width: 1rem;
    padding: 0 auto;
    text-align: right;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .top_txt_r img {
    width: .23rem;
    height: .23rem;

  }
  .goods_details {
    margin: .08rem 0;
    background: #fff;;
  }

  .goods_details_content {
    font-size: .12rem;
    color: #777;
    padding: .12rem .15rem;
    overflow: hidden;
    max-height: 0.61rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }
  .goods_details_content_more {
    background: #ffffff;
    padding: 0.08rem 0;
    text-align: center;
    height: 0.4rem;
    position: relative;
  }
  .goods_details_content_more_btn {
    font-size: 0.1rem;
    color: #00e0A8;
    border: 1px solid #00e0A8;
    background: #ffffff;
    padding: 0.01rem 0.05rem;
    border-radius: 0.15rem;
    display: inline-block;
    position: absolute;
    top: 0.13rem;
    left: 42%;
  }
  .you_like {
    margin: .08rem 0;
  }
  .like_goods {
    /*width: 1.85rem;*/
    width: 1.55rem;
    height: 2.28rem;
    background: #fff;
    float: left;
    margin: 0.025rem;
  }
  .like_img {
    width: 1.55rem;
    height: 1.7rem;
  }
  .like_txt {
    height: 0.58rem;
    padding: .1rem;
  }
  .like_goods_title {
    font-size: .13rem;
    font-weight: bold;
    color:#333;
  }
  .like_goods_pri {
    font-size: .15rem;
    color:#333;
    display: flex;
    line-height: .15rem;
    margin-top: .06rem;
  }
  .like_goods_price {
    font-size: .15rem;
    font-weight: bold;
    color: #D4483B;
    width: 50%;
    text-align: left;
  }

  .like_goods_sales {
    font-size: .11rem;
    color: #333;
    width: 50%;
    text-align: right;
  }
  .user_img_c img {
    position: relative;
    bottom: 0.55rem;
    left: 0;
  }
  .user_comment_xin>span {
    display: inline-block;
    width: 0.1rem;
    height: 0.1rem;
  }
  .bottom_fixed_shop div {
    width: 50%;
    text-align: center;
  }
  .bottom_fixed_shop_l img {
    width: 0.2rem;
    height: 0.2rem;
  }
  .bottom_fixed_shop_l_shop {
    display: block;
    position: absolute;
    bottom: -0.13rem;
    left: 0.25rem;
  }
  .bottom_fixed_shop_r img {
    width: 0.23rem;
    height: 0.23rem;
  }
</style>



