<script type="es6">
export default {
  name: 'OutFit',
  data () {
    return {
        juli:"12.13"
    }
  },
  mounted:function(){
    this.user_xin_seo();
    this.swiper_c();
    this.find_address();
  },
  methods:{
    //用户评论功能优化
    user_xin_seo:function(){
      $('.user_comment_all').each(function(){
        var star = $(this).attr('data-xin_light'); //获取data 一个为5，一个为4
        /*alert(star)*/
        $(this).find('.user_comment_xin img').attr("src","/static/images/star/xin_hui.png");
        $(this).find('.user_comment_xin img').each(function(i,ele){ //ele当前的元素， i当前元素的索引
          if(i<star){
            // alert(i);
            // alert(ele);
            $(ele).attr("src","/static/images/star/xin_red.png");
          }
        });
      });
    },
    //轮播图
    swiper_c:function () {
      var mySwiper = new Swiper ('.swiper-container', {
        direction: 'horizontal',
        loop: true,
        delay:3000,
        disableOnInteraction:true,
        pagination: {
          el: '.swiper-pagination',
          clickable :true
        },
        centeredSlides : true,
        slidesPerView :'auto',
        centeredslides:true
      });
      var mySwiperTwo = new Swiper ('.swiper-container_you_like', {
        direction: 'horizontal',
        loop: false,
        slidesPerView :'auto'
      });
    },
    //机构详情介绍-查看更多
    outfit_details:function(){
      var sas = $('.outfit_details_content_more_btn').text();
      if(sas == "查看更多") {
        $('.outfit_details_content').attr('style', 'max-height:6rem;');
        $('.outfit_details_content_more_btn').text("收起更多");
      }else{
        $('.outfit_details_content').attr('style', 'max-height:0.61rem;');
        $('.outfit_details_content_more_btn').text("查看更多");
      }
    },
    //测评--加载更多优化
    user_comment_more:function(){
      var list_height = $('.user_comment_list').outerHeight();
      $('.user_comment_all').each(function(i,ele){ //ele当前的元素， i当前元素的索引
        var all_height = (i+1)*1.2;
        var max_height = 'max-height:'+ all_height +'rem;';
        console.log(i,max_height);
        if (list_height <= 320){
          $('.user_comment_list').attr('style',max_height);
        }else{
          $('.user_comment_more').text("————没有了————");
        }
      });
    },
    //定位
    find_address:function(){
      var that = this;
      var geolocation = new qq.maps.Geolocation("OB4BZ-D4W3U-B7VVO-4PJWW-6TKDJ-WPB77", "bayin");
      var positionNum = 0;
      var options = {timeout: 8000};
      function showPosition(position) {
        var adCode = position.adCode;//邮政编码
        var nation = position.nation;//中国
        var city = position.city; //城市
        var addr = position.addr; //详细地址
        var lat = position.lat; //
        var lng = position.lng; //火星坐标
        var detailsAddress = city + addr;
        $('.outfit_msg_r_bottom').text(detailsAddress);
        //console.log("lng："+lng);
        //console.log("lat："+lat);
        var self_jl = ju_li(lng,lat);
        //console.log("距离："+self_jl);
        that.juli = self_jl.toFixed(2);
      }
      function showErr() {
        alert("定位失败，请稍后再试...");
      }
      function dingwei_c(){
        //定位
        geolocation.getLocation(showPosition, showErr, options);
      }
      dingwei_c();
      //定位后计算距离
      function ju_li(self_lng,self_lat){
        var lat,lng;
        lat = 34.169022;
        lng = 108.960750;
        //lng = 108.931653;
        //lat = 34.222289;
        return Math.sqrt(Math.pow((lat - self_lat),2) + Math.pow((lng - self_lng),2))*100;
      }
    }
  }
}
</script>

