<template>
  <div class="content">
    <!-- 课程视频-->
    <div class="top_video">
      <video class="top_video_c" @click="pause_video" :src="top_video.src" height="100%" width="100%" :poster="top_video.img" >抱歉，您的浏览器不支持此视频格式。。。</video>
      <div class="top_video_zc"></div>
      <div class="top_video_zc_txt" @click="play_video">试看30s</div>
    </div>
    <!-- 老师资料-->
    <div class="teacher_info">
      <div class="teacher_logo">
        <div class="teacher_logo_c"><img :src="teacher_msg.logo" alt="老师"/></div>
      </div>
      <div class="teacher_msg">
        <div class="teacher_name">{{teacher_msg.name}}</div>
        <div class="teacher_school">{{teacher_msg.school}}</div>
        <div class="teacher_details">{{teacher_msg.details}}</div>
      </div>
    </div>
    <!-- 课程介绍-->
    <div class="class_details">
      <p class="ci_title">课程介绍</p>
      <p class="class_details_content"><span>{{class_details_content}}</span></p>
      <p class="class_details_content_more"><button class="class_details_content_more_btn" @click="goods_details">查看更多</button></p>
    </div>
    <!-- 猜你喜欢-->
    <div class="you_like">
      <div class="ci_title">猜你喜欢</div>
      <div class="like_class">
        <div class="swiper-container_you_like">
          <div class="swiper-wrapper">
            <div class="swiper-slide" v-for="like in you_like_lists">
              <div class="like_class_all">
                <div class="like_img"><img :src="like.src" alt="课程"/></div>
                <div class="like_name">
                  {{like.name}}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
    <!-- 评论-->
    <div class="user_comment">
      <div class="ci_title">
        评论（{{user_comment_num}}条）
      </div>
      <div class="user_comment_list">
        <div class="user_comment_all" v-for="user_comment in user_comment_lists" :data-xin_light="user_comment.light_num">
          <div class="user_img">
            <div class="user_img_c"><img :src="user_comment.logo" alt="图像"/></div>
          </div>
          <div class="user_info">
            <div class="user_name">{{user_comment.name}}
                        <span class="user_comment_xin">
                            <span><img class="user_comment_xin_img" :src="user_comment.no_light_star_src"/></span>
                            <span><img class="user_comment_xin_img" :src="user_comment.no_light_star_src"/></span>
                            <span><img class="user_comment_xin_img" :src="user_comment.no_light_star_src"/></span>
                            <span><img class="user_comment_xin_img" :src="user_comment.no_light_star_src"/></span>
                            <span><img class="user_comment_xin_img" :src="user_comment.no_light_star_src"/></span>
                        </span>
            </div>
            <div class="user_comment_time">{{user_comment.time}}</div>
            <div class="user_comment_content">{{user_comment.content}}</div>
          </div>
        </div>
      </div>
      <div class="user_comment_more" @click="user_comment_more">————加载更多————</div>
    </div>
    <!-- 底部固定-->
    <div class="bottom_fixed">
      <div class="bottom_fixed_shop">
        <div class="bottom_fixed_shop_r" @click="add_self"><img class="bottom_fixed_shop_r_img" src="/static/images/shoucang_hui.png"/><span class="bottom_fixed_shop_l_sc">未收藏</span></div>
      </div>
      <div class="bottom_fixed_buy" @click="buy_goods">购买（￥{{buy_price}}）</div>
    </div>
    <div class="bottom_fixed_bottom"></div>
  </div>
</template>

<script type="es6">
export default {
  name: 'index',
  data () {
    return {
      top_video:{
        src:"http://qiniu.eightyin.com/data/upload/zppgwg.mp4",
        img:"/static/images/test.jpg"
      },
      teacher_msg:{
        logo: "/static/images/store_logo.png",
        name:"祁文",
        school:"沈阳音乐学院",
        details:"毕业于沈阳音乐学院管弦系长笛专业，在教学中擅长于教学比赛，培养出多名优秀人才."
      },
      class_details_content:"萨克斯（英语：Saxophone，简称：Sax。）是一种木管乐器，但是管体通常是用黄铜制造，使得萨克斯管同时具有铜管类乐器的特性。有意思的是这点常造成归类上的分歧。从发声原理上来看萨克斯管和同样使用单簧片的单簧管更接近，所以一般归类为木管乐器。萨克斯管同时也是一种移调乐器，常见调性为降E和降B，也有C调和F调。萨克斯管以其烟斗状的外形为人所熟知。",
      you_like_lists:[
        {
          src: "/static/images/default.jpg",
          name: "长笛考级一级音阶"
        },
        {
          src: "/static/images/test.jpg",
          name: "长笛考级二级音阶"
        },
        {
          src: "/static/images/default.jpg",
          name: "长笛考级三级音阶"
        },
        {
          src: "/static/images/test.jpg",
          name: "长笛考级四级音阶"
        },
        {
          src: "/static/images/default.jpg",
          name: "长笛考级五级音阶"
        }
      ],
      user_comment_num:"5",
      user_comment_lists:[
        {
          light_num:"4",
          logo:"/static/images/store_logo.png",
          name:"皮皮虾",
          no_light_star_src:"/static/images/star/xin_hui.png",
          time:"2018-07-27  17:05:00",
          content:"这个课程很好，对我的帮助很大很喜欢，支持你们！"
        },
        {
          light_num:"2",
          logo:"/static/images/store_logo.png",
          name:"蓝胖纸",
          no_light_star_src:"/static/images/star/xin_hui.png",
          time:"2018-07-27  17:05:00",
          content:"这个课程很不好，对我的帮助几乎没有，差评！"
        },
        {
          light_num:"1",
          logo:"/static/images/store_logo.png",
          name:"灰姑凉",
          no_light_star_src:"/static/images/star/xin_hui.png",
          time:"2018-07-27  17:05:00",
          content:"这个课程很好，十分喜欢，支持既有加油加油！"
        },
        {
          light_num:"3",
          logo:"/static/images/store_logo.png",
          name:"甜甜圈",
          no_light_star_src:"/static/images/star/xin_hui.png",
          time:"2018-07-27  17:05:00",
          content:"66666666666666666！"
        },
        {
          light_num:"5",
          logo:"/static/images/store_logo.png",
          name:"小灰灰",
          no_light_star_src:"/static/images/star/xin_hui.png",
          time:"2018-07-27  17:05:00",
          content:"不是水军不来这，啦啦啦，水一水，经验到手，转身就走。！"
        }
      ],
      buy_price: "2.00"
    }
  },
  mounted:function(){
    this.user_xin_seo();
    this.swiper_c();
  },
  methods:{
    //用户评论功能优化
    user_xin_seo:function(){
      $('.user_comment_all').each(function(){
        var star = $(this).attr('data-xin_light'); //获取data 一个为5，一个为4
        /*alert(star)*/
        $(this).find('.user_comment_xin img').attr("src","/static/images/star/xin_hui.png");
        $(this).find('.user_comment_xin img').each(function(i,ele){ //ele当前的元素， i当前元素的索引
          if(i<star){
            $(ele).attr("src","/static/images/star/xin_red.png");
          }
        });
      });
    },
    //轮播图
    swiper_c:function () {
      var mySwiper = new Swiper ('.swiper-container', {
        direction: 'horizontal',
        loop: true,
        delay:3000,
        disableOnInteraction:true,
        pagination: {
          el: '.swiper-pagination',
          clickable :true
        },
        centeredSlides : true,
        slidesPerView :'auto',
        centeredslides:true
      });
      var mySwiperTwo = new Swiper ('.swiper-container_you_like', {
        direction: 'horizontal',
        loop: false,
        slidesPerView :'auto'
      });
    },
    //商品详情介绍
    goods_details:function(){
      var sas = $('.class_details_content_more_btn').text();
      if(sas == "查看更多") {
        $('.class_details_content').attr('style', 'max-height:600px;');
        $('.class_details_content_more_btn').text("收起更多");
      }else{
        $('.class_details_content').attr('style', 'max-height:0.61rem;');
        $('.class_details_content_more_btn').text("查看更多");
      }
    },
    //测评--加载更多优化
    user_comment_more:function(){
      var list_height = $('.user_comment_list').outerHeight();
      $('.user_comment_all').each(function(i,ele){ //ele当前的元素， i当前元素的索引
        var all_height = (i+1)*0.95;
        var max_height = 'max-height:'+ all_height +'rem;';
        //console.log(i,max_height);
        if (list_height <= 300){
          $('.user_comment_list').attr('style',max_height);
        }else{
          $('.user_comment_more').text("————没有了————");
        }
      });
    },
    //收藏
    add_self:function () {
      $('.bottom_fixed_shop_r_img').attr('src','/static/images/shoucang_light.png');
      $('.bottom_fixed_shop_l_sc').text("已收藏");
      $('.bottom_fixed_shop_l_sc').attr('style','color:#00e0a8');
    },
    //购买
    buy_goods:function () {
      alert("暂无法购买，请您静候更新哦~");
    },
    //点击遮罩层播放视频
    play_video:function(){
      $('.top_video_c').trigger('play');
      $('.top_video_zc').attr('style','display:none');
      $('.top_video_zc_txt').attr('style','display:none');
    },
    //点击视频停止播放
    pause_video:function(){
      $('.top_video_c').trigger('pause');
      $('.top_video_zc').attr('style','display:block');
      $('.top_video_zc_txt').attr('style','display:block');
    }
  }
}
</script>

<style scoped>
  .top_video {
    position: relative;
    text-align: center;
  }
  .top_video_zc {
    width: 100%;
    height: 100%;
    background: #000000;
    opacity: 0.3;
    position: absolute;
    top: 0;
    left: 0;
  }
  .top_video_zc_txt {
    background: #ffffff;
    color: #00e0A8;
    font-size: 0.13rem;
    text-align: center;
    display: inline-block;
    border-radius: 30px;
    width: 0.8rem;
    height: 0.24rem;
    line-height: 0.24rem;
    z-index: 10;
    position: absolute;
    top: 0.75rem;
    left: 1.25rem;
    opacity: 0.7;
  }
  .teacher_info {
    display: flex;
    background: #ffffff;
    margin: 0.08rem 0;
    margin-top: 0.04rem;
  }
  .teacher_logo {
    width: 0.75rem;
    padding: 0.1rem;
    padding-left: 0.15rem;
  }
  .teacher_logo_c {
    width: 0.5rem;
    height: 0.5rem;
  }
  .teacher_msg {
    padding-top: 0.19rem;
    padding-bottom: 0.08rem;
    padding-right: 0.05rem;
  }
  .teacher_name {
    opacity: 0.85;
    color: #000000;
    font-size: 0.14rem;
    font-weight: bold;
  }
  .teacher_school {
    opacity: 0.85;
    color: #000000;
    font-size: 0.11rem;

  }
  .teacher_details {
    opacity: 0.45;
    color: #000000;
    font-size: 0.11rem;
    font-weight: bold;
  }
  .class_details_content {
    background: #ffffff;
    padding: 0.15rem;
    padding-top: 0.05rem;
    color: #000000;
    font-size: 0.14rem;
    overflow: hidden;
    max-height: 0.61rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
  }
  .class_details_content_more {
    background: #ffffff;
    padding: 0.08rem 0;
    text-align: center;
  }
  .class_details_content_more_btn {
    font-size: 0.1rem;
    color: #00e0A8;
    border: 1px solid #00e0A8;
    background: #ffffff;
    padding: 0.01rem 0.05rem;
    border-radius: 0.15rem;
  }
  .class_details_content span {
    opacity: 0.65;
  }
  .you_like {
    margin: 0.08rem 0;
  }
  .like_class {
    background: #ffffff;
    padding: 0.1rem 0.15rem;
    overflow: hidden;
  }
  .like_class_all {
    padding-right: 0.05rem;
  }
  .swiper-wrapper {
    width: 50%;
  }
  .like_img {
    width: 1.4rem;
    height: 0.819rem;
  }
  .like_name {
    margin-top: 0.03rem;
    margin-bottom: 0.13rem;
    font-size: 0.12rem;
    opacity: 0.65;
    color: #000000;
  }
  .user_comment {
    background: #ffffff;
  }
  .user_comment_list {
    max-height: 1.88rem;
    overflow: hidden;
  }
  .user_comment_all {
    display: flex;
    padding: 0.05rem 0.15rem;
    padding-bottom: 0;
    background: #ffffff;
  }
  .user_info {
    padding-left: 0.08rem;
    border-bottom: 1px solid #dddddd;
    padding-bottom: 0.11rem;
    width: 2.4rem;
  }
  .user_img {
    width: 0.43rem;
  }
  .user_name {
    font-size: 0.13rem;
    opacity: 0.65;
    color: #000;
    position: relative;
  }
  .user_comment_xin {
    position: absolute;
    top: 0;
    right: 0;
  }
  .user_comment_xin span {
    display: inline-block;
    width: 0.15rem;
    height: 0.15rem;
    /*float: right;*/
  }
  .user_comment_time {
    font-size: 0.1rem;
    opacity: 0.65;
    color: #000;
    margin: 0.06rem 0;
  }
  .user_comment_content {
    font-size: 0.14rem;
    opacity: 0.85;
    color: #000;
  }
  .user_comment_more {
    background: #ffffff;
    text-align: center;
    padding: 0.25rem 0;
    opacity: 0.25;
    color: #000;
    font-size: 0.13rem;
  }
  .bottom_fixed {
    width: 100%;
    height: 0.55rem;
    background: #00e0A8;
    position: fixed;
    left: 0;
    bottom: 0;
    display: flex;
    border-top: 1px solid #dddddd;
    z-index: 99999;
  }
  .bottom_fixed_bottom {
    width: 100%;
    height: 0.55rem;
    background: #fff;
  }
  .bottom_fixed_shop {
    font-size: 0.11rem;
    color: #00e0A8;
    width: 0.64rem;
    background: #ffffff;
    line-height: 0.5rem;
    display: flex;
  }
  .bottom_fixed_shop_r {
    margin: 0 auto;
  }
  .bottom_fixed_shop_r img {
    width: 0.23rem;
    height: 0.23rem;
  }
  .bottom_fixed_shop_l_sc {
    display: block;
    position: absolute;
    bottom: -0.13rem;
    left: 0.15rem;
    color: #777;
  }
  .bottom_fixed_buy {
    font-size: 0.2rem;
    color: #ffffff;
    width: 2.538rem;;
    text-align: center;
    line-height: 0.55rem;
  }
  .bottom_fixed_shop_l_sc_sc {
    color: #00e0A8;
  }
</style>



