<template>
    <div class="content">
        <input type="file"  name="file" onchange="fileup(event)">
    </div>
</template>

<script type="es6">
export default {
  name: 'index',
  data () {
    return {

    }
  },
  methods:{
    fileup:function(e) {
  　　var formData = new FormData();
  　　formData.append('file', e.target.files[0]);
  　　var index = layer.load(0, {shade: false}); //0代表加载的风格，支持0-2  引用layer插件　　
  　　$.ajax({
  　　　　url: '/api/User/avatarUpload',  
  　　　　type: 'POST',
  　　　　cache: false,
  　　　　data: formData,
  　　　　processData: false,
  　　　　contentType: false
  　　}).done(function(res) {
  　　　　console.log(res.data.domain);
  　　　　console.log(res.data.filepath);

  　　　　$('#layui-layer-shade1,.layui-layer.layui-layer-loading').remove();  //去掉加载loading
  　　　　$('#ticket2').attr('src',res.data.filepath);  //给图片添加路径
  　　
  　　}).fail(function(res) {
  　　　　$('#layui-layer-shade1,#layui-layer1').remove();
  　　});
    }
  }
};

</script>

<style scoped>
  .content {
    text-align: center;
  }
</style>



